<template>
  <router-link
    :to="name"
    class="portrait"
    :class="{ portrait_active: activeChar }"
    draggable="false"
  >
    <img
      alt=""
      :src="require('@/assets/images/frame.png')"
      class="portrait__decoration"
      draggable="false"
    />
    <div
      class="portrait__image"
      :style="`background-image: url('${charPortrait}')`"
    />
  </router-link>
</template>

<script>
export default {
  name: 'cCharPortrait',
  props: {
    name: String,
  },
  computed: {
    charPortrait() {
      return require(`@/assets/images/characters/${this.name}.png`)
    },
    activeChar() {
      return this.$route.params.name === this.name
    },
  },
}
</script>

<style lang="scss">
$height: 150px;
$width: $height * 0.7;
$padding: $height / 30;

.portrait {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  width: $width;
  height: $height;
  padding: $padding;
  box-sizing: border-box;
  opacity: 0.5;

  &_active {
    opacity: 1;
  }

  &__image {
    display: block;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    width: 100%;
    height: 100%;
  }
  &__decoration {
    position: absolute;
    width: 100%;
    height: 100%;
  }
}
</style>
