<template>
  <div ref="log" class="log"></div>
</template>

<script>
export default {
  name: 'cLog',
  methods: {
    isCrit(result) {
      if (typeof result === 'number')
        return result === 1 ? 'log__entity-result crit' : 'log__entity-result'
      else {
        return result[0] === 1 && result[1] === 1 ? 'crit' : 'good'
      }
    },
    time() {
      const data = new Date()
      let hours = data.getHours(),
        minutes = data.getMinutes(),
        seconds = data.getSeconds()

      hours = hours >= 10 ? hours : '0' + hours
      minutes = minutes >= 10 ? minutes : '0' + minutes
      seconds = seconds >= 10 ? seconds : '0' + seconds
      return `${hours}:${minutes}:${seconds}`
    },
    renderNewLogEntity(data) {
      const { dice, charName, name, result, line, greater } = data
      const newLine = document.createElement('div')
      newLine.classList.add('log__entity', this.isCrit(result))
      newLine.innerHTML = `
      <div class="log__entity-greater-result">${greater}</div>
      <div class="log__entity-title">
        <div class="log__entity-name">${charName}</div>
        <div class="log__entity-roll-name">${name}</div>
        <div class="log__entity-time">${this.time()}</div>
      </div>
      <div class="log__entity-results">
        <div class="${this.isCrit(result[0])}">(d${dice[0]}) ${line[0]}</div>
        <div class="${this.isCrit(result[1])}">(d${dice[1]}) ${line[1]}</div>
      </div>
      `

      this.$refs.log.prepend(newLine)
    },
  },
  mounted() {
    this.$eventBus.$on('roll-done', this.renderNewLogEntity)
  },
  beforeDestroy() {
    this.$eventBus.$off('roll-done')
  },
}
</script>

<style lang="scss">
.crit {
  color: red;
}
.log {
  flex-grow: 1;
  height: 100%;
  padding: 8px;
  overflow-y: scroll;
  overflow-x: hidden;
  flex-flow: column-reverse;
  display: flex;

  &__entity {
    display: flex;
    font-size: 12px;
    border-bottom: 1px solid #555555;

    &-title {
      width: 101px;
    }
    &-results {
      margin-left: 4px;
      display: flex;
      flex-flow: column;
      justify-content: center;
    }
    &-name {
      font-weight: bold;
    }
    &-result {
    }
    &-greater-result {
      display: flex;
      justify-content: center;
      font-size: 20px;
      align-items: center;
      padding: 0 4px;
      font-weight: bold;
      width: 32px;
    }
  }
}
</style>
