<template>
  <div class="char" v-if="char">
    <h1 class="char__name">{{ char.name }}</h1>
    <div class="char__body">
      <div class="char__content">
        <div class="char__skills">
          <c-skill-column
            class="char__skills-column"
            :attr="char.attributes.dexterity"
            :char-name="char.name"
          />
          <c-skill-column
            class="char__skills-column"
            :attr="char.attributes.smarts"
            :char-name="char.name"
          />
          <div class="char__skills-column-wrapper">
            <c-skill-column
              class="char__skills-column"
              :attr="char.attributes.character"
              :char-name="char.name"
            />
            <c-skill-column
              class="char__skills-column"
              :attr="char.attributes.strength"
              :char-name="char.name"
            />
            <c-skill-column
              class="char__skills-column"
              :attr="char.attributes.endurance"
              :char-name="char.name"
            />
          </div>
        </div>
        <div class="char__e&h">
          <div class="char__e&h-column"></div>
          <div class="char__e&h-column"></div>
        </div>
        <div class="char__exp">
          <div class="char__exp-promotion"></div>
          <div class="char__exp-promotion"></div>
          <div class="char__exp-promotion"></div>
          <div class="char__exp-promotion"></div>
        </div>
      </div>
      <c-log class="char__log" />
    </div>
  </div>
</template>

<script>
import CSkillColumn from '@/components/cChar/cSkillColumn'
import CLog from '@/components/cLog'
export default {
  name: 'cChar',
  components: { CLog, CSkillColumn },
  props: {
    char: { type: Object },
  },
  computed: {},
  data() {
    return {
      meta: {
        name: '',
        player: '',
        concept: '',
        appearance: '',
      },
    }
  },
}
</script>

<style lang="scss">
.char {
  width: 100%;
  display: flex;
  flex-flow: column;
  height: 100%;

  &__name {
    margin: 0 0 8px;
    border-bottom: 2px solid;
  }
  &__body {
    display: flex;
    flex-grow: 1;
    height: calc(100% - 53px);
  }
  &__log {
    background-color: #fff;
    height: 100%;
    flex-grow: 1;
    margin-left: 20px;
    min-width: 400px;
  }
  &__content {
    width: 640px;
  }

  &__skills {
    display: flex;

    &-column-wrapper {
      & > * + * {
        margin-top: 20px;
      }
    }

    & > * + * {
      margin-left: 20px;
    }
  }

  &__physical-values {
  }

  &__value {
  }

  &__exp {
  }

  &__exp-promotion {
  }
}
</style>
