<template>
  <div class="sc">
    <div class="sc__entity sc__entity_attribute" @click="roll(attr)">
      <div class="sc__entity-title">{{ attr.name }}</div>
      <div class="sc__value-wrapper sc__value-wrapper_skill">
        <div class="sc__value-modify"></div>
        <div class="sc__value">
          {{ getDice(attr.level, attr.bonus) }}
        </div>
        <div class="sc__value-modify"></div>
      </div>
    </div>

    <div
      class="sc__entity"
      :class="{ sc__entity_magic: skill.hidden === false }"
      v-for="(skill, index) in skills"
      :key="index"
      @click="roll(skill)"
    >
      <div class="sc__entity-title">{{ skill.name }}</div>
      <div class="sc__value-wrapper sc__value-wrapper_skill">
        <div class="sc__value-modify"></div>
        <div
          class="sc__value"
          :class="{ sc__value_unlearned: skill.level === 0 }"
        >
          {{ getDice(skill.level, skill.bonus) }}
        </div>
        <div class="sc__value-modify"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'cSkillColumn',
  props: {
    attr: Object,
    charName: String,
  },
  computed: {
    skills() {
      if (!!this.attr && !!this.attr.skills) {
        let result = {}
        Object.keys(this.attr.skills).forEach(item => {
          if (!this.attr.skills[item].hidden) {
            result[item] = this.attr.skills[item]
          }
        })
        return result
      }

      return {}
    },
  },
  methods: {
    getDice(level, bonus) {
      if (level === 0) {
        return 'd4' + this.parseBonus(bonus - 2)
      } else {
        return `d${(level + 1) * 2}` + this.parseBonus(bonus)
      }
    },
    parseBonus(bonus) {
      if (bonus > 0) {
        return `+${bonus}`
      } else if (bonus < 0) {
        return `-${bonus * -1}`
      } else {
        return '+0'
      }
    },
    roll(value) {
      const dice = value.level === 0 ? 4 : (value.level + 1) * 2
      const bonus = value.level === 0 ? value.bonus - 2 : value.bonus
      let line = ['', ''],
        result = [0, 0]
      const loop = (dice, i) => {
        let roll = Math.floor(Math.random() * dice) + 1
        result[i] += roll
        if (line[i] === '') line[i] += roll
        else line[i] += ' + ' + roll

        if (roll === dice) loop(dice, i)
      }
      loop(dice, 0)
      loop(6, 1)

      let greater = (result[0] > result[1] ? result[0] : result[1]) + bonus

      this.$eventBus.$emit('roll-done', {
        charName: this.charName,
        name: value.name,
        dice: [dice, 6],
        line: [
          this.resultRendering(result[0], line[0], bonus),
          this.resultRendering(result[1], line[1], bonus),
        ],
        result,
        greater: greater > 0 ? greater : 0,
      })

      console.log(result + ' = ' + line)
    },
    resultRendering(result, line, bonus) {
      if (bonus === 0) {
        return `${result} <= ${line}`
      } else {
        const sign = bonus < 0 ? '-' : '+'
        const modBonus = bonus < 0 ? bonus * -1 : bonus
        const modResult = result + bonus < 0 ? 0 : result + bonus
        return `${modResult} <= ${result}${sign}${modBonus} <= ${line}`
      }
    },
  },
}
</script>

<style lang="scss" scoped>
.sc {
  width: 200px;
  min-height: 25px;

  &__entity {
    display: flex;
    justify-content: space-between;
    cursor: pointer;

    &_magic {
      color: #5181b8;
    }
    &:hover {
      opacity: 1;
      font-weight: bold;
    }

    & + * {
      margin-top: 4px;
    }
    &_attribute {
      border-bottom: 1px solid;
      margin-bottom: 8px;
      font-weight: bold;

      &:hover {
        border-bottom: 2px solid;
        margin-bottom: 7px;
      }
    }

    &-title {
    }
  }
  &__value {
    &_unlearned {
      opacity: 0.5;
    }
    &-wrapper {
      &_vertical {
      }
      &_horizontal {
      }
      &_attribute {
        font-weight: bold;
      }
      &_skill {
      }
    }
    &-modify {
    }
  }
  &__content {
  }
}
</style>
